<template>
  <div id="dashboard">
    <MediaPlayer />
    <div id="data">
      <div class="sector">
        <Top />
        <Indexes :token="token" />
        <Legal />
      </div>
      <div class="sector">
        <Market :token="token" />
        <Legal />
      </div>
    </div>
  </div>
</template>

<script>
import MediaPlayer from '../components/MediaPlayer.vue';
import Top from '../components/dashboard/Top.vue';
import Indexes from '../components/dashboard/Indexes.vue';
import Market from '../components/dashboard/Market.vue';
import Legal from '../components/Legal.vue';

import axios from 'axios';

export default {
  name: 'dashboard',
  components: {
    MediaPlayer,
    Top,
    Indexes,
    Market,
    Legal
  },
  data: () => {
    return {
      token: null,
      timer: null,
      counter: 0,
      error: ''
    };
  },
  mounted() {
    console.log('Dashboard mounted!');
    this.getToken();
    this.timer = setInterval(this.getToken, 60000 * 5);

    // process.env.NODE_ENV;
    console.log(
      'process.env.VUE_APP_VALOR_FUTURO_URL',
      process.env.VUE_APP_VALOR_FUTURO_URL
    );
  },
  methods: {
    getToken() {
      let headers = {
        headers: {
          'Content-type': 'text/plain'
        }
      };
      let params = 'UserName=bsantander&Password=Santand3R&grant_type=password';

      axios
        .post(
          process.env.VUE_APP_VALOR_FUTURO_URL + '/vftoken/oauth/token',
          params,
          headers
        )
        .then(response => {
          console.log(response.data);

          this.token = response.data.access_token;
          this.counter++;
        })
        .catch(error => {
          if (error.response) {
            console.log('data', error.response.data);
            //console.log('status', error.response.status);
            //console.log('headers', error.response.headers);
          }
          console.log('config', error.config);
        });

      console.log('counter', this.counter);
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

<style scoped>
#dashboard {
  width: 960px;
  height: 1080px;
}

#data {
  position: relative;
}

.sector {
  width: 100%;
  height: 50%;
}
</style
